import echarts from 'echarts'

export var lineChart;
export const chartUtils = {
  initColumn: (id, legend, title, color, data) => {
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      title: {
        subtext: title
      },
      color: color,
      grid: {
        left: '1%',
        right: '2%',
        containLabel: true
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        y:'bottom',
        data: legend,
        itemHeight:10,
        itemWidth:12,
        textStyle: {
          fontSize: 10
        }
      },
      calculable: true,
      xAxis: [
        {
          type: 'category',
          data: [''],
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: data
    };
    myChart.setOption(option);
  },
  initPie:(id,legend,title,color,data)=>{
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      color: color,
      title: {
        subtext: title,
        textStyle: {
          fontSize: 12,
        }
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
      },
      legend: {
        // orient: 'vertical',
        y:'bottom',
        data: legend,
        itemHeight:10,
        itemWidth:12,
        textStyle: {
          fontSize: 10
        }
      },
      series: [
        {
          name: title,
          type: 'pie',
          radius: ['50%', '70%'],
          center: ['50%','50%'],  //第一个百分比是左右移动，50%居中，越小越靠左，反之亦然；第二个百分比是上下移动，50%居中，越小越靠上反之亦然。
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:data
        }
      ]
    };
    myChart.setOption(option);

  },

  initLine: (id, xData, yData1, yData2, name1, name2,oneMax,oneMin,twoMax,twoMix,endVal) => {
    const myChart3 = echarts.init(document.getElementById(id));
    const y2 =
      {
        name: name2,
        type: 'value',
        minInterval: 1,
        splitNumber: 6,
        min: twoMix,
        max: twoMax,
        scale:true
      };
    const data2 =
      {
        name: name2,
        type: 'line',
        smooth: true,
        yAxisIndex: 1,
        data: yData2,
      };
    var option = {
      color: ['#92D050','#0000ff'],
      grid: {
        left: '1%',
        right: '5%',
        bottom: 30,
        top: 50,
        containLabel: true
      },
      tooltip: {
        trigger: 'axis',
        triggerOn: 'click',
        axisPointer: {
          type: 'cross',
          lineStyle: {
            color: 'red',
            width: 2
          },
          crossStyle: {
            color: 'red',
            width: 2
          },
          label: {
            backgroundColor: '#62abef'
          }
        }
      },
      legend: {
        show: false
      },
      calculable: true,
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: xData,
        axisLabel: {
          interval:30
        }
      },
      dataZoom: [{
        type: 'slider',
        show: true,
        xAxisIndex: [0],
        left: '9%',
        bottom: -5,
        start: 0,
        end: endVal //初始化滚动条
      }],
      yAxis: [
        {
          name: name1,
          type: 'value',
          minInterval: 1,
          splitNumber: 6,
          min: oneMin,
          max: oneMax,
          scale:true
        }
      ],
      series: [
        {
          name: name1,
          type: 'line',
          smooth: true,
          areaStyle: {
            origin: 'start'
          },
          data: yData1

        }
      ]
    };
    if(yData2) {
      option.yAxis.push(y2);
      option.series.push(data2);
    } else {
      option.yAxis = option.yAxis[0];
      option.series = option.series[0];
    }
    document.getElementById(id).setAttribute('_echarts_instance_', '');
    myChart3.setOption(option);
    lineChart = myChart3;
  }

};




