import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import outdoorTest from '@/views/outdoorTest/outdoorTest'
import test from '@/views/test/test'
import outdoorTest5G from '@/views/outdoorTest5G/outdoorTest5G'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/outdoorTest',
      name: 'outdoorTest',
      component: resolve => require(['../views/outdoorTest/outdoorTest.vue'], resolve)
    },
    {
      path: '/test',
      name: 'test',
      component: resolve => require(['../views/test/test.vue'], resolve)
    },
    {
      path: '/outdoorTest5G',
      name: 'outdoorTest5G',
      component: resolve => require(['../views/outdoorTest5G/outdoorTest5G.vue'], resolve)
    },
  ]
})



