<template>
  <div id="count">
    <a-card size="small" title="无线指标" v-if="wxzbBeanList.length > 0">
      <a-row type="flex" justify="space-between" :gutter="24">
        <a-col :span="14" class="column_kpi">

          <div class="ftpContainer">
            <div class="tableTd" v-for="item of wxzbBeanList">
              <b>{{item.title}}</b>
              <span>{{item.data}}</span>
            </div>
          </div>

        </a-col>
        <a-col :span="10">
          <a-row>
            <a-col :span="24">
              <div id="wireless1" style="height:300px;"></div>
            </a-col>
            <a-col :span="24">
              <div id="wireless2" style="height:300px;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

    <a-card size="small" title="FTP指标" v-if="ftpzbBeanList.length > 0">
      <a-row type="flex" justify="space-around" :gutter="8">
        <a-col :span="9" class="column_kpi">
          <div class="ftpContainer">
            <div class="tableTd" v-for="item of ftpzbBeanList">
              <b>{{item.title}}</b>
              <span>{{item.data}}</span>
            </div>
          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="ftpchart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
              <div id="ftpchart2" style="height:35vh;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

    <a-card size="small" title="语音指标" v-if="yyzbbeanList.length > 0">
      <a-row type="flex" justify="space-around" :gutter="8">
        <a-col :span="9"  class="column_kpi">

          <div class="ftpContainer">
            <div class="tableTd" v-for="item of yyzbbeanList">
              <b>{{item.title}}</b>
              <span>{{item.data}}</span>
            </div>
          </div>

        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="volteChart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
              <div id="volteChart2" style="height:35vh;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

    <a-card size="small" title="Ping/Http指标" v-if="pingzbBeanList.length > 0 || httpzbBeanList.length > 0">
      <a-row type="flex" justify="space-around" :gutter="8">
        <a-col :span="9" class="column_kpi">
          <div >
            <h4 style="text-align: left;margin: 10px 0px">Ping指标：</h4>

            <div class="ftpContainer">
              <div class="tableTd" v-for="item of pingzbBeanList">
                <b>{{item.title}}</b>
                <span>{{item.data}}</span>
              </div>
            </div>

          </div>
          <div >
            <h4 style="text-align: left;margin: 10px 0px">Http指标：</h4>

            <div class="ftpContainer">
              <div class="tableTd" v-for="item of httpzbBeanList">
                <b>{{item.title}}</b>
                <span>{{item.data}}</span>
              </div>
            </div>

          </div>
        </a-col>
        <a-col :span="15">
          <a-row>
            <a-col :span="12">
              <div id="httpChart1" style="height:35vh;"></div>
            </a-col>
            <a-col :span="12">
              <div id="httpChart2" style="height:35vh;"></div>
            </a-col>
          </a-row>
        </a-col>
      </a-row>
    </a-card>

  </div>
</template>

<script>
  import {chartUtils} from '@/utils/chart.js'
  import {OutdoorTestApi} from '@/http/api/outdoorTest5G'
  export default {
    name: '',
    components: {

    },
    props:['rowData'],
    data() {
      return {
        rowClassName:(record,index)=>{
          let className = index % 2 ? 'shallow_gray': '';
          return className
        },
        countData:{wxzbBean:{},ftpzbBean:{},yyzbBean:{},pingzbBean:{}},
        ftpzbBeanList: [],
        yyzbbeanList: [],
        pingzbBeanList: [],
        httpzbBeanList: [],
        wxzbBeanList: []
      }
    },
    beforeCreate() {

    },
    mounted() {
      this.initData();
    },
    methods: {
      async initData(){
        const params = {
          pid:this.rowData.id
        };
        const data = await OutdoorTestApi.queryAnalyzeData(params);
        this.countData=data.data.object;
        const rsrpLegend = [];
        const rsrpColor = [];
        const rsrpData = [];
        const sinrLegend = [];
        const sinrColor = [];
        const sinrData = [];
        const ulLegend = [];
        const ulColor = [];
        const ulData = [];
        const dlLegend = [];
        const dlColor = [];
        const dlData = [];
        if(data.data.success){
          // 处理ftp指标数据
          if (this.countData.ftpzbBean) {
            for(let item in this.countData.ftpzbBean) {
              this.ftpzbBeanList.push({title: item + '：', data: this.countData.ftpzbBean[item]});
            }
          }
          if (this.ftpzbBeanList.length%2 == 1) {
            this.ftpzbBeanList.push({title: null, data: null});
          }
          // 处理语音指标数据
          if (this.countData.yyzbbean) {
            for(let item in this.countData.yyzbbean) {
              this.yyzbbeanList.push({title: item + '：', data: this.countData.yyzbbean[item]});
            }
          }
          if (this.yyzbbeanList.length%2 == 1) {
            this.yyzbbeanList.push({title: null, data: null});
          }
          // 处理ping指标数据
          if (this.countData.pingzbBean) {
            for(let item in this.countData.pingzbBean) {
              this.pingzbBeanList.push({title: item + '：', data: this.countData.pingzbBean[item]});
            }
          }
          if (this.pingzbBeanList.length%2 == 1) {
            this.pingzbBeanList.push({title: null, data: null});
          }
          // 处理http指标数据
          if (this.countData.httpzbBean) {
            for(let item in this.countData.httpzbBean) {
              this.httpzbBeanList.push({title: item + '：', data: this.countData.httpzbBean[item]});
            }
          }
          if (this.httpzbBeanList.length%2 == 1) {
            this.httpzbBeanList.push({title: null, data: null});
          }

          if (this.countData.wxzbBean5g) {
            for(let item in this.countData.wxzbBean5g) {
              this.wxzbBeanList.push({title: item + '：', data: this.countData.wxzbBean5g[item], type: '5g'});
            }
          }
          if (this.countData.wxzbBean4g) {
            for(let item in this.countData.wxzbBean4g) {
              this.wxzbBeanList.push({title: item + '：', data: this.countData.wxzbBean4g[item], type: '4g'});
            }
          }
          if (this.wxzbBeanList.length%2 == 1) {
            this.wxzbBeanList.push({title: null, data: null});
          }

          data.data.object.range.map(x=>{
            if(x.item =='RSRP'){
              x.content.map(v=>{
                rsrpLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                rsrpColor.push(v.colorVal);
                rsrpData.push({
                  name:v.minVal+'~'+v.maxVal,
                  type: 'bar',
                  data: [v.num]
                });
              })
            }else if(x.item =='SINR'){
              x.content.map(v=>{
                sinrLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                sinrColor.push(v.colorVal);
                sinrData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }else if(x.item =='UL'){
              x.content.map(v=>{
                ulLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                ulColor.push(v.colorVal);
                ulData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }else{
              x.content.map(v=>{
                dlLegend.push(v.minVal+'~'+v.maxVal);
                if(v.colorVal == ''){
                  v.colorVal = '#000000'
                }
                dlColor.push(v.colorVal);
                dlData.push({
                  value: [v.num],
                  name:v.minVal+'~'+v.maxVal
                });
              })
            }
          })
        }
        if (this.wxzbBeanList && this.wxzbBeanList.length > 0) {
          setTimeout(() => {
            chartUtils.initColumn("wireless1",rsrpLegend,'RSRP',rsrpColor,rsrpData);
            chartUtils.initPie("wireless2",sinrLegend,'SINR',sinrColor,sinrData);
          }, 200);
        }
        if (this.ftpzbBeanList && this.ftpzbBeanList.length > 0) {
          setTimeout(() => {
            chartUtils.initPie("ftpchart1",ulLegend,'UL速率分布',ulColor,ulData);
            chartUtils.initPie("ftpchart2",dlLegend,'DL速率分布',dlColor,dlData);
          }, 200);
        }
        this.initKpis(data.data.object);
      },
      initKpis(data){
        const color = ['#51B1F9','#FB6669'];
        const volteLegend1 = ['接通次数','未接通次数'];
        const volteData1 = [{value:data.CALLSUCC,name:'接通次数'},{value:data.CALLFAIL,name:'未接通次数'}];
        let successRatio1 = '';
        if(data.CALLSUCC+data.CALLFAIL==0){
          successRatio1 = 0;
        }else{
          successRatio1 = (data.CALLSUCC / (data.CALLSUCC + data.CALLFAIL)).toFixed(2)*100
        }

        const volteLegend2 = ['接通次数', '掉话次数'];
        const volteData2 = [{value:data.CALLSUCC,name:'接通次数'},{value:data.CALLUNLINE,name:'掉话次数'}];
        let successRatio2 = '';
        if(data.CALLSUCC+data.CALLUNLINE==0){
          successRatio2 = 0;
        }else{
          successRatio2 = (data.CALLUNLINE / (data.CALLSUCC + data.CALLUNLINE)).toFixed(2)*100
        }

        const httpLegend1 = ['Ping成功次数', 'Ping失败次数'];
        const httpData1 = [{value:data.PINGSUCC,name:'Ping成功次数'},{value:data.PINGFAIL,name:'Ping失败次数'}];
        let httpRatio1 = '';
        if(data.PINGSUCC+data.PINGFAIL==0){
          httpRatio1 = 0;
        }else{
          httpRatio1 = (data.PINGSUCC / (data.PINGSUCC + data.PINGFAIL)).toFixed(2)*100
        }

        const httpLegend2 = ['Http成功次数', 'Http失败次数'];
        const httpData2 = [{value:data.HTTPSUCC,name:'Http成功次数'},{value:data.HTTPFAIL,name:'Http失败次数'}];
        let httpRatio2 = '';
        if(data.PINGSUCC+data.PINGFAIL==0){
          httpRatio2 = 0;
        }else{
          httpRatio2 = (data.HTTPSUCC / (data.HTTPSUCC + data.HTTPFAIL)).toFixed(2) *100
        }
        if (this.yyzbbeanList && this.yyzbbeanList.length > 0) {
          setTimeout(() => {
            chartUtils.initPie("volteChart1",volteLegend1,'接通率：'+successRatio1+'%',color,volteData1);
            chartUtils.initPie("volteChart2",volteLegend2,'掉话率：'+successRatio2+'%',color,volteData2);
          }, 200);
        }
         if (this.pingzbBeanList && this.pingzbBeanList.length > 0) {
           setTimeout(() => {
             chartUtils.initPie("httpChart1", httpLegend1, 'Ping成功率：' + httpRatio1 + '%', color, httpData1);
           }, 200);

           if (this.httpzbBeanList && this.httpzbBeanList.length > 0) {
             setTimeout(() => {
               chartUtils.initPie("httpChart2", httpLegend2, 'Http成功率：' + httpRatio2 + '%', color, httpData2);
             }, 200);
           }
         }

      }
    }
  }
</script>

<style>
  @import "../../../../static/css/style.css";

  .column_kpi{
    border-right: 1px solid #dddddd;
  }
  table {
    border-collapse: collapse;
  }
  table td {
    border:1px solid #dddddd;
    padding:5px 20px;
    text-align: left;
  }
  .ftpContainer{
    text-align: left;
    font-size: 12px;
  }
  .tableTd {
    width: 50%;
  }
  .ftpContainer>div:nth-child(2n) {
    border-left: 0;
  }
  .ftpContainer>div {
    border-top:1px solid #dddddd;
    border-left:1px solid #dddddd;
    border-right:1px solid #dddddd;
    padding:5px 10px;
    text-align: left;
    display: inline-block;
    height: 32px;
    vertical-align: top;
    white-space: nowrap;
  }
  .ftpContainer>div:nth-last-child(2),
  .ftpContainer>div:nth-last-child(1){
    border-bottom: 1px solid #dddddd;
  }

</style>



