<template>

  <div id="playBack" style="height: 888px;">

    <div class="loading-contain" v-if="loading">
      <div style="width: 200px;margin: auto;padding-top: 20%;">
        <a-progress :percent="progressVal" status="active" />
      </div>
    </div>

    <div class="selectPane">

      <div class="showMenu" style="display: inline-block;">

        <a-popover placement="bottom">
          <template slot="content">
            <a-checkbox-group :default-value = 'activePaneId' @change="handleChange">
            <div v-for="item of paneList">
              <a-checkbox :value="item.value" :disabled="item.disabled">{{item.label}}</a-checkbox>
            </div>
            </a-checkbox-group>
          </template>
          <a-button>选择显示面板</a-button>
        </a-popover>

      </div>

    </div>

    <div class="parentContainer" style="position: relative;width: 100%;height: 888px;padding: 10px;">
      <vdr :x="layout[0].x" :y="layout[0].y" :w="layout[0].w" :h="layout[0].h" @resizestop="onResizeStop" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @resizing="resizedEventMap" @activated="activePaneIndex = 0">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="地图">
          <div slot="extra">
            <div style="padding: 3px;background: #fff">
              <a-icon :type="rollBackName" @click="rollBack" theme="twoTone"
                      style="font-size: 14px;vertical-align: middle;cursor: pointer"
              />
              <a-icon type="exclamation-circle" @click="resetRollBack" theme="twoTone"
                      style="font-size: 14px;vertical-align: middle;cursor: pointer"/>
              <progress-bar :percent="percent" width="160px" leftBg="greenyellow" bgc="#ccc" ballBgc="red"
                            class="procress-bar" height="10px"
                            @pbar-drag="dragBar" @pbar-seek="seekBar"></progress-bar>
              <a-select v-model="speed" style="width: 40px" size="small">
                <a-select-option :value="1">1x</a-select-option>
                <a-select-option :value="0.5">2x</a-select-option>
                <a-select-option :value="0.25">4x</a-select-option>
              </a-select>

              <div class="showMenu" style="display: inline-block;">
                <a-dropdown>
                  <a-button>覆盖分析</a-button>
                  <a-menu slot="overlay">
                    <a-menu-item :class="activeAlone?'lineActive':''">
                      <div @click="aloneLine()">小区连线</div>
                    </a-menu-item>
                    <a-menu-item :class="activeAll?'lineActive':''">
                      <div @click="allLine()">全网连线</div>
                    </a-menu-item>
                  </a-menu>
                </a-dropdown>
              </div>

              <div class="showMenu" style="display: inline-block;">
                <a-dropdown>
                  <a-button disabled>PCI优化</a-button>
                  <a-menu slot="overlay">
                    <a-menu-item>
                      <div>同频PCI</div>
                    </a-menu-item>
                    <a-menu-item>
                      <div>同频模三</div>
                    </a-menu-item>
                  </a-menu>
                </a-dropdown>
              </div>

            </div>
          </div>
          <div id="map" style="width: 100%;height: 100%;position: relative">
            <div class="ciSearch">
              <a-input-search placeholder="请输入CI" enter-button="查询" @search="queryByCi" />
            </div>
            <div :class="ifShowLayer?'mapLeftToolsActive top1Jj':'mapLeftTools top1Jj'" @click="showLayer()">图层</div>
            <div :class="ifShowLegend?'mapLeftToolsActive top2Jj':'mapLeftTools top2Jj'"  @click="showLegendPane()">图例</div>

            <div class="leaflet-table" v-show="ifShowTable">
              <a-icon type="close" style="float: right;cursor: pointer;color: red;font-size: 14px" @click="closeDetail()" />
              <div class="showDetail" v-if="cellNameList.length>0">
                <a-select v-model="cellName" style="width: 120px" size="small" @change="changeSiteName">
                  <a-select-option v-for="item in cellNameList" :value="item.value" :key="item.value">{{item.siteName}}</a-select-option>
                </a-select>
                <p>小区名字：{{this.siteInfoData.lte_site_name}}</p>
                <p>网络：{{this.siteInfoData.lte_net}}</p>
                <div>
                  <p>eNodeB：{{this.siteInfoData.lte_enodebid}}</p>
                  <p>cell：{{this.siteInfoData.lte_cell}}</p>
                </div>
                <div>
                  <p>ECI：{{this.siteInfoData.lte_ecgi}}</p>
                  <p>PCI：{{this.siteInfoData.lte_phycellid}}</p>
                </div>
                <div>
                  <p>EARFCN：{{this.siteInfoData.lte_earfcn}}</p>
                  <p>TAC：{{this.siteInfoData.lte_tac}}</p>
                </div>
                <div>
                  <p>经度：{{this.siteInfoData.lte_longitude}}</p>
                  <p>纬度：{{this.siteInfoData.lte_latitude}}</p>
                </div>
                <div>
                  <p>方位角：{{this.siteInfoData.lte_azimuth}}</p>
                  <p>站高：{{this.siteInfoData.lte_site_tall}}</p>
                </div>
                <div>
                  <p>机械下倾角：{{this.siteInfoData.lte_mechanical_downdip}}</p>
                  <p>电子倾角：{{this.siteInfoData.lte_electronic_downdip}}</p>
                </div>

              </div>
              <div v-if="cellNameList.length==0" style="text-align: center;color: #999;line-height: 240px;">暂无数据</div>
            </div>

            <div style="width: 100%;height: 100%;position: relative;overflow: visible;">

              <div class="legendSetting" v-if="ifShowLegend">

                <div>
                  <table>
                    <tr>
                      <td>显示</td>
                      <td colspan="2">
                        <a-checkbox :checked="showLegendParam" @change="showLegendParam = !showLegendParam"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>采样点详情</td>
                      <td colspan="2">
                        <a-checkbox :checked="showLegendDetailParam" @change="showLegendDetailParam = !showLegendDetailParam"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>指标</td>
                      <td colspan="2">
                        <a-select v-model="legendRadio" @change="changeLegendType" style="width: 100px;position: relative;">
                          <a-select-option v-for="item of typeList" :value="item" :key="item">
                            {{item}}
                          </a-select-option>
                        </a-select>
                      </td>
                    </tr>
                    <tr>
                      <td colspan="3" style="text-align: center;">
                        <span class="addLegend" @click="changeLegendNum('plus')">增加</span>
                        <span :class="this.legends[legendRadio].content.length>1?'deleteLegend':'notDelete'" @click="changeLegendNum('minus')">删除</span>
                      </td>
                    </tr>
                    <tr class="colorContain" v-for="item in legends[legendRadio].index">
                      <td>
                        <a-input type="color" class="showColor" v-model="legends[legendRadio].content[item-1].colorVal"
                                 @change="value => setVal(value, item,'colorVal')" />
                      </td>
                      <td>
                        <a-input  class="showColor" v-model="legends[legendRadio].content[item-1].minVal"
                                  @change="value => setVal(value, item,'minVal')" />
                      </td>
                      <td>
                        <a-input  class="showColor" v-model="legends[legendRadio].content[item-1].maxVal"
                                  @change="value => setVal(value, item,'maxVal')" />
                      </td>
                    </tr>
                    <tr>
                      <td colspan="3" style="text-align: center;">
                        <span class="addLegend" @click="saveLegend">保存</span>
                        <span class="cancelLegend" @click="cancelLegend()">取消</span>
                      </td>
                    </tr>
                  </table>
                </div>

              </div>

              <div class="legendSetting" v-show="ifShowLayer">

                <div>
                  <table>
                    <tr>
                      <td>小区</td>
                      <td>4G</td>
                      <td>
                        <a-checkbox :checked="layerData.stationFlag4g" @change="layerData.stationFlag4g = !layerData.stationFlag4g"></a-checkbox>
                      </td>
                      <td>5G</td>
                      <td>
                        <a-checkbox :checked="layerData.stationFlag5g" @change="layerData.stationFlag5g = !layerData.stationFlag5g"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td></td>
                      <td>室内</td>
                      <td>
                        <a-checkbox :checked="layerData.indoorFlag" @change="layerData.indoorFlag = !layerData.indoorFlag"></a-checkbox>
                      </td>
                      <td>室外</td>
                      <td>
                        <a-checkbox :checked="layerData.outdoorFlag" @change="layerData.outdoorFlag = !layerData.outdoorFlag"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>拉线</td>
                      <td>服务小区</td>
                      <td>
                        <a-checkbox :checked="layerData.serviceCellFlag" @change="layerData.serviceCellFlag = !layerData.serviceCellFlag"></a-checkbox>
                      </td>
                      <td>邻区</td>
                      <td>
                        <a-checkbox :checked="layerData.neighborhoodFlag" @change="layerData.neighborhoodFlag = !layerData.neighborhoodFlag"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>显示轨迹数值</td>
                      <td colspan="4" style="text-align: center;">
                        <a-checkbox :checked="layerData.showPointValFlag" @change="layerData.showPointValFlag = !layerData.showPointValFlag"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td>轨迹大小</td>
                      <td colspan="4" style="text-align: center;">
                        <a-select style="width: 80px" v-model="pointSize">
                          <a-select-option v-for="(item,index) of pointList" :value="item.value" :key="item.value">
                            <div :class="'showPoint point'+index"></div>
                          </a-select-option>
                        </a-select>
                      </td>
                    </tr>
                    <tr>
                      <td>事件</td>
                      <td>正常</td>
                      <td>
                        <a-checkbox :checked="layerData.eventNormalFlag" @change="layerData.eventNormalFlag = !layerData.eventNormalFlag"></a-checkbox>
                      </td>
                      <td>异常</td>
                      <td>
                        <a-checkbox :checked="layerData.eventAbnormalFlag" @change="layerData.eventAbnormalFlag = !layerData.eventAbnormalFlag"></a-checkbox>
                      </td>
                    </tr>
                    <tr>
                      <td colspan="5" style="text-align: center;">
                        <span class="addLegend" @click="saveLayer()">保存</span>
                        <span class="cancelLegend" @click="cancelLayer()">取消</span>
                      </td>
                    </tr>
                  </table>
                </div>

              </div>

            </div>

            <div class="legendModule" v-show="legendData.showFlag == '1'">
              <div style="text-align: left;font-weight: bold;" v-if="legendData.content && legendData.content.length > 0">{{legendData.item}}(dBm)</div>
              <div style="max-height: 100px;overflow: auto;">
                <table v-if="legendData.content && legendData.content.length > 0">
                  <tr class="colorContain" v-for="item in legendData.content">
                    <td>
                      <div class="showColorBlock" :style='{background:item.colorVal}'></div>
                    </td>
                    <td style="padding: 0 10px;">
                      ({{item.minVal}}, {{item.maxVal}}]
                    </td>
                    <td v-show="legendData.pointDetail == '1'" style="padding: 0 10px;">{{item.num}}</td>
                    <td v-show="legendData.pointDetail == '1'" style="padding: 0 10px;">{{item.numPerRatio}}</td>
                  </tr>
                </table>
              </div>
            </div>

          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(2) > -1" :x="layout[1].x" :y="layout[1].y" :w="layout[1].w" :h="layout[1].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @resizing="resizedEventLine" @resizestop="onResizeStop" @activated="activePaneIndex = 1">
        <a-card  style="width: 100%;height: 100%;position: relative;" size="small" title="折线图">
          <div class="header-btn">
            <a-button type="primary" @click="cancelLine()">指标</a-button>
          </div>
          <div style="height: 100%;overflow: auto;position: relative;">
            <div id="lineChartContain" style="width: 100%;height: 100%;"></div>
            <div class="lineSetModal" v-if="showLinePane">
              <table>
                <tr>
                  <td colspan="3">
                    <div class="showBoth">
                      <div>指标1</div>
                      <div>
                        <a-select v-model="lineData.type1" style="width: 80px" size="small">
                          <a-select-option v-for="item in typeList" :value="item" :key="item">{{item}}</a-select-option>
                        </a-select>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="3">
                    <div class="showBoth">
                      <div>指标2</div>
                      <div>
                        <a-select v-model="lineData.type2" :allowClear="true" style="width: 80px" size="small">
                          <a-select-option v-for="item in typeList" :value="item" :key="item">{{item}}</a-select-option>
                        </a-select>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td>指标范围</td>
                  <td>最大值</td>
                  <td>最小值</td>
                </tr>
                <tr v-for="item of lineData.content">
                  <td>{{item.name}}</td>
                  <td><a-input v-model="item.min" /></td>
                  <td><a-input v-model="item.max" /></td>
                </tr>
                <tr>
                  <td colspan="3" style="text-align: center;">
                    <a-button type="primary" @click="saveLine()">保存</a-button>
                    <a-button style="margin-left: 20px;" @click="cancelLine()">取消</a-button>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(3) > -1" :x="layout[2].x" :y="layout[2].y" :w="layout[2].w" :h="layout[2].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 2"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="信令">
          <div slot="extra">
            <div style="padding: 3px;background: #fff;display: flex;">
              <a-input style="flex: 1;" v-model="messageTypeFlag" @change="messageTypeIndex=0" />
              <a-button style="margin: 3px 5px;" :disabled="!messageTypeFlag" type="primary" size="small" @click="queryFlag('message',null)" >查询</a-button>
              <a-button style="margin: 3px 5px;" :disabled="!msgRecord.active" type="primary" size="small" @click="queryFlag('message','up')" >向上</a-button>
              <a-button style="margin: 3px 5px;" :disabled="!msgRecord.active" type="primary" size="small" @click="queryFlag('message','down')" >向下</a-button>
            </div>
          </div>

          <div class="xngd" style="margin-right: 8px;color: #202569;font-weight: 500;">
            <div>singalType</div>
            <div>messageType</div>
            <div>timestamp</div>
            <div>channelType</div>
          </div>

          <div id="msgDiv" style="height:calc(100% - 30px);overflow-y:scroll;" @scroll="hanldeScroll">
            <div :style="{height:scorllH}">
              <div class="item_box" :style="`transform:translateY(${offSetY}px); `">
                <div v-for="item of msgData" @click="clickMsg(item,true)" @dblclick="showDetail"
                     :class="[{ shallow: item.active }, 'xngd']">
                  <div>
                    <a-icon type="arrow-up" v-if="item.mSingaType=='ul'"
                            style="color: red;font-size: 12px;cursor: pointer"></a-icon>
                    <a-icon type="arrow-down" v-if="item.mSingaType=='dl'"
                            style="color: green;font-size: 12px;cursor: pointer"></a-icon>
                  </div>
                  <div :title="item.mMessageType">{{item.mMessageType}}</div>
                  <div>{{item.timestamp}}</div>
                  <div :title="item.mMessageType" class="textOverflow">{{item.mChannelType}}</div>
                </div>
              </div>
            </div>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(4) > -1" :x="layout[3].x" :y="layout[3].y" :w="layout[3].w" :h="layout[3].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 3"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="事件">
          <div slot="extra">
            <div style="padding: 3px;background: #fff;display: flex;">
              <a-input style="flex: 1" v-model="eventTypeFlag"  @change="eventTypeIndex=0" />
              <a-button style="margin: 3px 5px;" :disabled="!eventTypeFlag" type="primary" size="small" @click="queryFlag('event', null)">查询</a-button>
              <a-button style="margin: 3px 5px;" :disabled="!eventRecord.active" type="primary" size="small" @click="queryFlag('event', 'up')">向上</a-button>
              <a-button style="margin: 3px 5px;" :disabled="!eventRecord.active" type="primary" size="small" @click="queryFlag('event', 'down')">向下</a-button>
            </div>
          </div>

          <div class="sjgd" style="margin-right: 8px;color: #202569;font-weight: 500;">
            <div>mEventType</div>
            <div>mEvent</div>
            <div>mTimestamp</div>
          </div>

          <div id="evtDiv" style="height:calc(100% - 30px);overflow-y:scroll;" @scroll="hanldeScroll1">
            <div :style="{height:scorllH1}">
              <div class="item_box" :style="`transform:translateY(${offSetY1}px); `">
                <div v-for="item of eventData" @click="clickEvt(item,true)" :class="[{ shallow: item.active }, 'sjgd']">
                  <div :title="item.mEventType">{{item.mEventType}}</div>
                  <div :title="item.mEvent">{{item.mEvent}}</div>
                  <div>{{item.timestamp}}</div>
                </div>
              </div>
            </div>
          </div>

        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(5) > -1" :x="layout[4].x" :y="layout[4].y" :w="layout[4].w" :h="layout[4].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 4"  @resizestop="onResizeStop">
        <a-card style="height: 100%;width: 100%;position: relative;" size="small" title=" 4G LTE Info">
          <div class="sameTable">
            <a-row>
              <a-col :span="12" v-for="item in Object.keys(LteData.cellInfo4g)">
                <div class='sameTr'>
                  <div :title="item">{{item}}</div>
                  <div>{{LteData.cellInfo4g[item]}}</div>
                </div>
              </a-col>

              <a-col :span="12" v-if="Object.keys(LteData.cellInfo4g).length % 2 == 1">
                <div class='sameTr'>
                  <div></div>
                  <div></div>
                </div>
              </a-col>

            </a-row>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(6) > -1" :x="layout[5].x" :y="layout[5].y" :w="layout[5].w" :h="layout[5].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 5"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="4G LTE Throughput">
          <div style="height: 100%;overflow: auto">
            <table>
              <tr>
                <th>Thr(Mbps)</th>
                <th>DL</th>
                <th>UL</th>
              </tr>
              <tr v-for="item in Object.keys(LteData.grate4g)">
                <td>{{item}}</td>
                <td v-for="data in LteData.grate4g[item].split('~')">{{data}}</td>
              </tr>
            </table>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(7) > -1" :x="layout[6].x" :y="layout[6].y" :w="layout[6].w" :h="layout[6].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 6"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="4G LTE Serving+Neighboring Cells">
          <div style="height: 100%;overflow: auto">
            <a-table bordered size="small" :rowClassName="rowClassName"
                     :rowKey="(record,index) => index"
                     :pagination="false"
                     :columns="LTEColumns4g"
                     :dataSource="LteData.nearOrMain4g"
            ></a-table>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(8) > -1" :x="layout[7].x" :y="layout[7].y" :w="layout[7].w" :h="layout[7].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 7"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="5G LTE Serving+Neighboring Cells">
          <div style="height: 100%;overflow: auto">
            <a-table bordered size="small" :rowClassName="rowClassName"
                     :rowKey="(record,index) => index"
                     :pagination="false"
                     :columns="LTEColumns5g"
                     :dataSource="LteData.nearOrMain5g"
            ></a-table>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(9) > -1" :x="layout[8].x" :y="layout[8].y" :w="layout[8].w" :h="layout[8].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 8"  @resizestop="onResizeStop">
        <a-card  style="height: 100%;width: 100%;position: relative;" size="small" title=" 5G LTE Info">
          <div class="sameTable">
            <a-row>
              <a-col :span="12" v-for="item in Object.keys(LteData.cellInfo5g)">
                <div class='sameTr'>
                  <div :title="item">{{item}}</div>
                  <div>{{LteData.cellInfo5g[item]}}</div>
                </div>
              </a-col>

              <a-col :span="12" v-if="Object.keys(LteData.cellInfo5g).length % 2 == 1">
                <div class='sameTr'>
                  <div></div>
                  <div></div>
                </div>
              </a-col>

            </a-row>
          </div>
        </a-card>
      </vdr>
      <vdr v-show="activePaneId.indexOf(10) > -1" :x="layout[9].x" :y="layout[9].y" :w="layout[9].w" :h="layout[9].h" @dragstop="onDragStop"
           drag-handle=".ant-card-head-title" parent=".parentContainer" @activated="activePaneIndex = 9"  @resizestop="onResizeStop">
        <a-card style="width: 100%;height: 100%;position: relative;" size="small" title="5G LTE Throughput">
          <div style="height: 100%;overflow: auto">
            <table>
              <tr>
                <th>Thr(Mbps)</th>
                <th>DL</th>
                <th>UL</th>
              </tr>
              <tr v-for="item in Object.keys(LteData.grate5g)">
                <td>{{item}}</td>
                <td v-for="data in LteData.grate5g[item].split('~')">{{data}}</td>
              </tr>
            </table>
          </div>
        </a-card>
      </vdr>
    </div>

    <message-info ref="messageInfo" :record="msgRecord"></message-info>
  </div>
</template>

<script>

  let map;
  let marker;
  let eventMarker;  //事件类型图标
  var outdoor4g = L.icon({
    iconUrl: '../../../../static/img/outdoor4g.png',
    iconSize: [24, 25],
    iconAnchor: [12, 25],
