    methods: {
      initMap() {
        map = L.map("map", {
          center: this.map_config.center,
          zoom: this.map_config.zoom
        });
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Map");
        mapUtils.addLayer(map,this.map_config,"TianDiTu.Normal.Annotion");
        L.control.scale({maxWidth: 50, metric: true, imperial: false, position: 'bottomleft'}).addTo(map);
        L.control.polylineMeasure ({
          position:'topleft',
          unit:'metres',
          showBearings:false,
          clearMeasurementsOnStop: false,
          showClearControl: true,
          showUnitControl: false}).addTo (map);
        map.on("moveend", (e) => {
          this.ifShowCells();
        });
        map.on("zoomend", (e) => {
          const zoom = map.getZoom();
          if (zoom < 14 && this.makerCluster.length > 0) {
            if (this.layerData.stationFlag4g && canvasLayer4g) {
              canvasLayer4g.addTo(map);
            }
            if (this.layerData.stationFlag5g && canvasLayer5g) {
              canvasLayer5g.addTo(map);
            }
            if (this.cellsIndoor4g.length > 0) {
              for (let item of this.cellsIndoor4g) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.cellsIndoor5g.length > 0) {
              for (let item of this.cellsIndoor5g) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.cellsOutdoor4g.length > 0) {
              for (let item of this.cellsOutdoor4g) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.cellsOutdoor5g.length > 0) {
              for (let item of this.cellsOutdoor5g) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.groupsList.length > 0) {
              for (let item of this.groupsList) {
                mapUtils.removeLayer(item.group, map);
              }
            }
            if (this.layerData.eventNormalFlag && this.eventAbnormal.length > 0) {
              for (let item of this.eventAbnormal) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.layerData.eventNormalFlag && this.eventNormal.length > 0) {
              for (let item of this.eventNormal) {
                mapUtils.removeLayer(item,map);
              }
            }
          } else {
            if (canvasLayer4g) {
              map.removeLayer(canvasLayer4g);
            }
            if (canvasLayer5g) {
              map.removeLayer(canvasLayer5g);
            }
            if (this.cellsIndoor4g.length > 0) {
              for (let item of this.cellsIndoor4g) {
                item.addTo(map);
              }
            }
            if (this.cellsIndoor5g.length > 0) {
              for (let item of this.cellsIndoor5g) {
                item.addTo(map);
              }
            }
            if (this.cellsOutdoor4g.length > 0) {
              for (let item of this.cellsOutdoor4g) {
                item.addTo(map);
              }
            }
            if (this.cellsOutdoor5g.length > 0) {
              for (let item of this.cellsOutdoor5g) {
                item.addTo(map);
              }
            }
            if (this.groupsList && this.groupsList.length > 0) {
              for(let line of this.groupsList) {
                line.group.addTo(map);
              }
            }
            // 添加正常事件覆盖物
            if (this.layerData.eventNormalFlag && this.eventNormal.length > 0) {
              for (let item of this.eventNormal) {
                item.addTo(map);
              }
            }
            // 添加异常事件覆盖物
            if (this.layerData.eventNormalFlag && this.eventAbnormal.length > 0) {
              for (let item of this.eventAbnormal) {
                item.addTo(map);
              }
            }
          }
        })
        this.queryMainLog();
      },
      async queryMainLog() {
        this.loading = true;

        var interval = setInterval(() => {
          if (this.progressVal >= 99) {
            clearInterval(interval);
          } else if (this.progressVal < 99 && this.progressVal>= 90) {
            this.progressVal += 1;
          } else {
            this.progressVal += 10;
          }
        }, 100);
        const params = {
          type: "logmain5g",
          index: "logmain5g",
          pid: this.rowData.id + 'lfllf',
          termMap: {"pid.keyword": this.rowData.id},
          limit: 1000,  //固定
          sortMap: {"testTimeMill": "ASC"},  //固定
          revelFields: ["id","latitude","longitude","testTime","downLoadSpeed","upLoadSpeed",
            "normalEventType","abNormalEventType","rsrp","sinr","ssrsrp","sssinr","signalEventBeans","signalBeans"],  //固定
          revelPk: "false"  //固定
        };
        const data = await OutdoorTestApi.queryMainLog(params);
        const latlngs = [];
        if (data.data) {
          this.mainLogData = data.data;
          data.data.some((x, index) => {
            if (x.signalBeans && x.signalBeans.length > 0) {
              x.signalBeans.map(m => {
                m.latitude = Number(x.latitude);
                m.longitude = Number(x.longitude);
                m.active = false;
                m.timestamp = timestampToSeconds(m.mTimestamp);
              });
              this.msgTableData.push(...x.signalBeans);   //获取msg所有表格数据
            }
            if (x.signalEventBeans && x.signalEventBeans.length > 0) {
              x.signalEventBeans.map((m,index) => {
                m.latitude = Number(x.latitude);
                m.longitude = Number(x.longitude);
                m.active = false;
                m.timestamp = timestampToSeconds(m.mTimestamp);
              });
              this.eventTableData.push(...x.signalEventBeans);   //获取event所有表格数据
            }
            latlngs.push({
              mid: x.id,
              latitude: x.latitude,
              longitude: x.longitude,
              rsrp:x.rsrp,
              sinr:x.sinr,
              ssrsrp:x.ssrsrp,
              sssinr:x.sssinr,
              ul:x.upLoadSpeed,
              dl:x.downLoadSpeed
            })   //获取所有的经纬度信息
          });
          //对经纬度数据进行去重操作
          this.latlngArray = [];
          latlngs.map(i => {
            return Object.keys(i).map(k => i[k]).join('')
          }).filter((item, index, strarr) => {
            if (strarr.indexOf(item) === index) {
              this.latlngArray.push(latlngs[index])
            }
          });
          // 计算信令的滚动条高度
          this.scorllH =  30*this.msgTableData.length + "px";
          // 计算可视区域能展示的个数 这里可以随机多加几个让滚动有个临界区间，避免向下滑动时元素直接替换
          this.lastTime = new Date().getTime();
          // 计算事件的滚动条高度
          this.scorllH1 =  30*this.eventTableData.length + "px";
          // 计算可视区域能展示的个数 这里可以随机多加几个让滚动有个临界区间，避免向下滑动时元素直接替换
          this.lastTime1 = new Date().getTime();
          // 默认展示
          this.msgTableData.forEach((v, index) => v.index = index);
          this.eventTableData.forEach((v, index) => v.index = index);
          this.msgData = this.msgTableData.slice(0, 20);
          this.eventData = this.eventTableData.slice(0, 20);
          const latitude = data.data[data.data.length - 1].latitude;
          const longitude = data.data[data.data.length - 1].longitude;
          this.initLteInfo(data.data[data.data.length - 1].id);
          this.initPoints(this.latlngArray);
          this.queryEventType();
        }
        if (this.layerData.stationFlag5g) {
          this.renderFullCells('simgc5g');
        }
        if (this.layerData.stationFlag4g) {
          this.renderFullCells('simgc');
        }
      },
      initPoints(latlngs) {
        if(circleGroups){
          circleGroups.clearLayers();
        }
        if (valGroups) {
          valGroups.clearLayers();
        }
        if(latlngs.length){
          map.setView([latlngs[latlngs.length - 1].latitude, latlngs[latlngs.length - 1].longitude], 16);
          latlngs.forEach((x,index)=> {
            let color = "";
            //筛选图例显示的相关颜色
            if (this.legendObj.content) {
              this.legendObj.content.forEach(v=>{
                const value = Number(x[this.legendObj.item.toLowerCase()]);
                if(Number(v.minVal) < value && value < Number(v.maxVal)){
                  color = v.colorVal;
                }
              });
            }
            const circle = L.circle([x.latitude, x.longitude], {
              color: color,
              fillColor: color,
              fillOpacity: 1,
              radius: this.pointSize,
              lng:x.longitude,
              lat:x.latitude,
              index:index,
              mid: x.mid,
              id: x.mid
            }).addTo(circleGroups);

            const rsrp = L.divIcon({
              html: parseInt(x.rsrp),
              className: 'my-div-icon',
              iconSize:20
            });
            const sinr = L.divIcon({
              html: parseInt(x.sinr),
              className: 'my-div-icon',
              iconSize:20
            });
            const ssrsrp = L.divIcon({
              html: parseInt(x.ssrsrp),
              className: 'my-div-icon',
              iconSize:20
            });
            const sssinr = L.divIcon({
              html: parseInt(x.sssinr),
              className: 'my-div-icon',
              iconSize:20
            });
            const dl = L.divIcon({
              html: '<span>'+parseInt(x.dl)+'</span>',
              className: 'my-div-icon',
              iconSize:20
            });
            const ul = L.divIcon({
              html: parseInt(x.ul),
              className: 'my-div-icon',
              iconSize:20
            });
            var marker;
            if (x.rsrp && this.legendData.item == 'RSRP') {
              marker = L.marker([x.latitude, x.longitude], { icon: rsrp });
            }
            if (x.sinr && this.legendData.item == 'SJNR') {
              marker = L.marker([x.latitude, x.longitude], { icon: sinr });
            }
            if (x.ssrsrp && this.legendData.item == 'SSRSRP') {
              marker = L.marker([x.latitude, x.longitude], { icon: ssrsrp });
            }
            if (x.sssinr && this.legendData.item == 'SSSINR') {
              marker = L.marker([x.latitude, x.longitude], { icon: sssinr });
            }
            if (x.ul && this.legendData.item == 'UL') {
              marker = L.marker([x.latitude, x.longitude], { icon: ul });
            }
            if (x.dl && this.legendData.item == 'DL') {
              marker = L.marker([x.latitude, x.longitude], { icon: dl });
            }
            x.color = color;
            circle.on('click', (e) => {
              this.circleEvt(x,index,latlngs);
            });
            if (marker) {
              marker.on('click',() => {
                this.circleEvt(x,index,latlngs);
              });
              marker.addTo(valGroups);
            }
          });
          circleGroups.addTo(map);
          if (this.layerData.showPointValFlag) {
            valGroups.addTo(map);
          } else {
            valGroups.clearLayers();
          }

        }
      },
      highLightPoint(lat, lng) {
        mapUtils.removeLayer(marker,map);
        var index = this.latlngArray.findIndex(v => (v.latitude == lat && v.longitude == lng));
        var color = this.latlngArray[index].color;
        marker = L.circle([lat, lng],
          {
            color: '#fff',
            fillColor: color?color:'#000',
            fillOpacity: 1,
            radius: (this.pointSize + 2)
          }).addTo(map);
        map.setView([lat, lng]);
      },
      //使用搜索框对eventType进行筛选时，根据eventType不同，显示不同的图标
      highLightEventPoint(lat, lng,eventType, type){
        const icon = L.icon({
          iconUrl: '../../../../static/eventImg/'+eventType+'.png',
          iconSize: [40, 48],
          iconAnchor:[40,48]    //图标偏移量
        });
        eventMarker = L.marker([lat, lng],
          {
            icon:icon
          }).addTo(map);
        if (type == 'abNormalEvent') {
          this.eventAbnormal.push(eventMarker);
        } else {
          this.eventNormal.push(eventMarker);
        }
        // map.setView([lat, lng]);
      },
      //根据左下右上经纬度渲染主服务小区和邻区扇去
      async renderCells(type, outdoor, indoor) {
        //左上右下经纬度拼接
        const leftup = map.getBounds().getNorthWest().lng + "_" + map.getBounds().getNorthWest().lat;
        const rightdown = map.getBounds().getSouthEast().lng + "_" + map.getBounds().getSouthEast().lat;

        const latlngs = leftup + "_" + rightdown;
        /*if (leftupLg == rightdownLng || leftupLat == rightdownLat) {
          return;
        }*/
        const params = {
          type: type,  //固定
          pid: latlngs,  //var strModel=bsswlng+"_"+bsnelat+"_"+bsnelng+"_"+bsswlat;
          index: type,//固定
          limit: 1000,//固定
          termMap: {},//固定
          revelFields: ["lte_azimuth", "lte_ci", "lte_longitude2", "lte_latitude2", "lte_site_type","lte_phycellid"],//固定
          revelPk: "false"//固定
        };
        const data = await OutdoorTestApi.boundingBoxQuery(params);

        data.data.map(x => {
          var cells;
          var colorVal = x.lte_phycellid % 3;
          if (type == 'simgc' && this.ciList4g.indexOf(x.lte_ci) == -1) {
            if (outdoor && x.lte_site_type == outdoor) {
                cells = L.marker([x.lte_latitude2, x.lte_longitude2],
                  {icon: outdoor4g, alt: '4g'+x.lte_ci, rotationAngle: x.lte_azimuth,lat:x.lte_latitude2,lng:x.lte_longitude2})
                  .addTo(map);
              cells.on('click',(e)=>{
                clearTimeout(this.timeClick);
                this.timeClick = setTimeout(()=>{
                  this.initParamsInfo('4g'+x.lte_ci,e.target.options.rotationAngle,e.target.options.lat,e.target.options.lng, 'simgc');
                },500);
              });
              this.ciList4g.push(x.lte_ci);
              cells.on('dblclick', (e) => {
                this.dblclickCell(e,'0',this.colorList[colorVal]);
              });
              this.cellsOutdoor4g.push(cells);
            }
            if (indoor && x.lte_site_type == indoor) {
                cells = L.marker([x.lte_latitude2, x.lte_longitude2],
                  {icon: indoor4g, alt: '4g'+x.lte_ci, rotationAngle: x.lte_azimuth,lat:x.lte_latitude2,lng:x.lte_longitude2})
                  .addTo(map);
              cells.on('click',(e)=>{
                clearTimeout(this.timeClick);
                this.timeClick = setTimeout(()=>{
                  this.initParamsInfo('4g'+x.lte_ci,e.target.options.rotationAngle,e.target.options.lat,e.target.options.lng, 'simgc');
                },500);
              });
              this.ciList4g.push(x.lte_ci);
              cells.on('dblclick', (e) => {
                this.dblclickCell(e,'0',this.colorList[colorVal]);
              });
              this.cellsIndoor4g.push(cells);
            }
          } else if (type == 'simgc5g' && this.ciList5g.indexOf(x.lte_ci) == -1) {
              if (outdoor && x.lte_site_type == outdoor) {
                cells = L.marker([x.lte_latitude2, x.lte_longitude2],
                  {icon: outdoor5g, alt: '5g'+x.lte_ci, rotationAngle: x.lte_azimuth,lat:x.lte_latitude2,lng:x.lte_longitude2})
                  .addTo(map);
                cells.on('click',(e)=>{
                  clearTimeout(this.timeClick);
                  this.timeClick = setTimeout(()=>{
                    this.initParamsInfo('5g'+x.lte_ci,e.target.options.rotationAngle,e.target.options.lat,e.target.options.lng, 'simgc5g');
                  },400);
                });
                this.ciList5g.push(x.lte_ci);
                cells.on('dblclick', (e) => {
                  this.dblclickCell(e,'1',this.colorList[colorVal]);
                });
                this.cellsOutdoor5g.push(cells);
              }

              if (indoor && x.lte_site_type == indoor) {
                cells = L.marker([x.lte_latitude2, x.lte_longitude2],
                  {icon: indoor5g, alt: '5g'+x.lte_ci, rotationAngle: x.lte_azimuth,lat:x.lte_latitude2,lng:x.lte_longitude2})
                  .addTo(map);
                cells.on('click',(e)=>{
                  clearTimeout(this.timeClick);
                  this.timeClick = setTimeout(()=>{
                    this.initParamsInfo('5g'+x.lte_ci,e.target.options.rotationAngle,e.target.options.lat,e.target.options.lng, 'simgc5g');
                  },400);
                });
                this.ciList5g.push(x.lte_ci);
                cells.on('dblclick', (e) => {
                  this.dblclickCell(e,'1',this.colorList[colorVal]);
                });
                this.cellsIndoor5g.push(cells);
              }
          }
        });
      },
      async initParamsInfo(ci,angle,lat,lng,type){
        map.setView([lat,lng]);
        var img = document.querySelector('img[alt="'+ci+'"]');
        var if4g = (ci.indexOf('4g') > -1);
        this.activeCell(if4g,ci,img);

        const params = {
          type: type,  //固定
          index: type,//固定
          limit: 1000,//固定
          termMap: {
            "lte_longitude2.keyword":lng,  //经度
            "lte_latitude2.keyword":lat,  //纬度
            "lte_azimuth.keyword":angle //方位角
          },//固定
          revelFields:["lte_site_name","lte_net","lte_longitude","lte_latitude","lte_azimuth","lte_enodebid","lte_cell","lte_ecgi","lte_earfcn","lte_tac","lte_phycellid","lte_site_tall","lte_mechanical_downdip","lte_electronic_downdip","lte_total_downdip","lte_ci","lte_city_name"]//固定
        };
        const data = await OutdoorTestApi.queryList(params);
        this.siteInfos = data.data;
        if(data.data && data.data.length > 0) {
          this.cellNameList = data.data.map(x => {
            return {
              siteName:x.lte_ci,
              value:x.pk
            }
          });
          this.cellName = this.cellNameList[0].value;
          this.siteInfoData = data.data[0];
        } else {
          this.cellNameList = [];
        }
        this.ifShowTable = true;
      },
      changeSiteName(e){
        this.siteInfos.map(x=>{
          if(x.pk==e){
            this.siteInfoData = x
          }
        })
      },
      //点击连线
      async renderPolyLines(lat, lng, id, type) {
        polyLineGroups4g.clearLayers();
        polyLineGroups5g.clearLayers();
        if (this.layerData.stationFlag5g || this.layerData.stationFlag4g) {
          var polyLineGroups;
          var redLine = {color: 'red', weight: 4};
          var greenLine = {color: 'green', weight: 1};
          if (type == '0') {
            polyLineGroups = polyLineGroups4g;
            redLine = {color: 'red', weight: 4, dashArray: '5,5'};
            greenLine = {color: 'green', weight: 1, dashArray: '5,5'};
          } else {
            polyLineGroups = polyLineGroups5g;
          }

          if (this.layerData.serviceCellFlag && (this.layerData.indoorFlag || this.layerData.outdoorFlag)) {
            const params = {
              "id": id,
              "gcType":type,
            };
            const data = await OutdoorTestApi.queryMainService(params);
            data.data.map(x => {
              let res = [[lat, lng], [x.lte_latitude2, x.lte_longitude2]];
              L.polyline(res, redLine).addTo(polyLineGroups);
            });
          }
          if (this.layerData.neighborhoodFlag && (this.layerData.indoorFlag || this.layerData.outdoorFlag)) {
            const paramsNears = {
              "id": id,
              "gcType":type,
            };
            const dataNear = await OutdoorTestApi.queryNearPoint(paramsNears);
            let latlngs = [];
            dataNear.data.map(x => {
              latlngs = [[lat, lng], [x.lte_latitude2, x.lte_longitude2]];
              L.polyline(latlngs, greenLine).addTo(polyLineGroups);
            });
          }
          polyLineGroups.addTo(map);
        }
      },
      //回放
      rollBack() {
        if(this.rollBackName == 'pause-circle'){
          this.rollBackName = 'play-circle';
          clearTimeout(this.latlngTimeOut);
        }else{
          this.playPoints();
          this.rollBackName = 'pause-circle'
        }
      },
      playPoints(){
        this.latlngTimeOut = setTimeout(() => {
          if (this.latlngIndex > this.latlngArray.length - 1) {
            this.latlngIndex = 0;
          }
          this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
          this.initLteInfo(this.latlngArray[this.latlngIndex].mid);
          this.showHighLight(this.latlngArray[this.latlngIndex], this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
          this.showHighLight(this.latlngArray[this.latlngIndex], this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
          if (this.layerData.stationFlag4g) {
            this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '0');
          }
          if (this.layerData.stationFlag5g) {
            this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '1');
          }
          this.latlngIndex++;
          this.percent = (this.latlngIndex/this.latlngArray.length).toFixed(2)*100;
          this.playPoints();
        }, this.speed*1000);
      },
      //重置
      resetRollBack(){
        this.latlngIndex = 0;
        this.percent =0;
        this.rollBackName = 'play-circle';
        clearTimeout(this.latlngTimeOut);
        this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.initLteInfo(this.latlngArray[this.latlngIndex].mid);
        this.showHighLight(this.latlngArray[this.latlngIndex], this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
        this.showHighLight(this.latlngArray[this.latlngIndex], this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '1');
        }
      },
      async renderFullCells(type) {
        const params = {
          "index": type,//固定
          "type": type,//固定
          "termMap": {'lte_site_type.keyword': 'outdoor'}
        };
        const data = await OutdoorTestApi.distinctGcLonLat(params);
        this.makerCluster = data.data;
        const fs = data.data.map(x => {
          let f = {
            "type": "Feature",
            "geometry": {"type": "Point", "coordinates": []}
          };
          f.geometry.coordinates[0] = Number(x['lte_longitude2']);
          f.geometry.coordinates[1] = Number(x['lte_latitude2']);
          return f;
        });
        mygeojson.features = fs;
        if (type == 'simgc5g') {
          canvasLayer5g = new VecLayer(mygeojson, options);
        } else {
          canvasLayer4g = new VecLayer(mygeojson, options);
        }
      },
      //表格上的查询操作
      queryFlag(e, str){
        if(e == 'message'){
          if(str == 'up') {
            this.searchMsgUp();
          } else if (str == 'down') {
            this.searchMsgDown();
          } else {
            this.activeMsgVal = this.messageTypeFlag;
            // 有选中项进行查询
            if (this.msgRecord.active) {
              this.searchMsgDown();
            } else {
              // 没有选中任何项进行查询
              const ifHave = this.msgTableData.some(v => v.mMessageType.indexOf(this.messageTypeFlag) >  -1);
              if (ifHave) {
                for (let v of this.msgTableData) {
                  if (v.mMessageType.indexOf(this.messageTypeFlag) > -1) {
                    this.activeMsgIndex = v.index;
                    this.clickMsg(v,false);
                    const top = v.index*30;
                    const divHeight = document.getElementById('msgDiv').offsetHeight + 30;
                    const scrollTop = document.getElementById('msgDiv').scrollTop + divHeight;
                    if (top > scrollTop) {
                      this.getScroll('lastTime', 'msgDiv', this.activeMsgIndex, 'msgData', this.msgTableData, 'offSetY');
                    }
                    break;
                  }
                }
              } else {
                this.activeMsgIndex = -1;
                this.$warning({
                  title: '提示',
                  content: '没有查找到匹配项',
                });
              }
            }
          }
        }else{
          if(str == 'up') {
            this.searchEvtUp();
          } else if (str == 'down') {
            this.searchEvtDown();
          } else {
            this.activeEvtVal = this.eventTypeFlag;
            // 有选中项进行查询
            if (this.eventRecord.active) {
              this.searchEvtDown();
            } else {
              // 没有选中任何项进行查询
              const ifHave = this.eventTableData.some(v => v.mEventType.indexOf(this.eventTypeFlag) >  -1);
              if (ifHave) {
                this.eventTableData.forEach(v => {
                  if (v.mEventType.indexOf(this.eventTypeFlag) > -1) {
                    this.activeEvtIndex = v.index;
                    this.clickEvt(v,false);
                    const top = v.index*30;
                    const divHeight = document.getElementById('evtDiv').offsetHeight + 30;
                    const scrollTop = document.getElementById('evtDiv').scrollTop + divHeight;
                    if (top > scrollTop) {
                      this.getScroll('lastTime1', 'evtDiv', this.activeEvtIndex, 'eventData', this.eventTableData, 'offSetY1');
                    }
                    return;
                  }
                })
              } else {
                this.activeEvtIndex = -1;
                this.$warning({
                  title: '提示',
                  content: '没有查找到匹配项',
                });
              }
            }
          }
        }
      },

      //保存legend
      async saveLegend(){
        let params = [{
          item: this.legendRadio,
          type: 1,
          content: this.legends[this.legendRadio].content,
          showFlag: this.showLegendParam? '1': '0',
          pointDetail: this.showLegendDetailParam? '1':'0'
        }];
        const data = await OutdoorTestApi.saveLegend(params);
        if(data.data.success){
          this.$message.success("图例修改成功！");
          this.ifShowLegend = false;
          this.initLegend();
        }
      },
      //初始化图例信息
      async initLegend(){
        const params = {
          type: 1,
          item: this.legendRadio,
          pid:  this.rowData.id
        };
        const data = await OutdoorTestApi.queryLegend(params);
        this.legendObj = JSON.parse(JSON.stringify(data.data.object[0]));
        this.legendData = JSON.parse(JSON.stringify(data.data.object[0]));
        this.showLegendParam = (this.legendObj.showFlag == '1')? true: false;
        this.showLegendDetailParam = (this.legendObj.pointDetail == '1')? true: false;
        if(data.data.object.length){
          this.legends[this.legendRadio].content = JSON.parse(JSON.stringify(data.data.object[0].content));
          this.legends[this.legendRadio].index = data.data.object[0].content.length;
        }
        this.initPoints(this.latlngArray);
      },
      setVal(e,index,key){
        this.legends[this.legendRadio].content[index-1][key]=e.target.value;
        /*实时更新视图*/
        this.$set(this.legends[this.legendRadio].content, index-1, this.legends[this.legendRadio].content[index-1]);
      },
      changeLegendNum(e) {
        if (e == 'plus') {
          this.legends[this.legendRadio].index++;
          this.legends[this.legendRadio].content[this.legends[this.legendRadio].index - 1] = {
            colorVal: '',
            minVal: '',
            maxVal: ''
          }
        } else {
          if (this.legends[this.legendRadio].content.length > 1) {
            const release = this.legends[this.legendRadio].content[this.legends[this.legendRadio].index - 1] //从后往前删除
            const index = this.legends[this.legendRadio].index - 1;
            this.legends[this.legendRadio].content.splice(index,1);
            this.legends[this.legendRadio].index--;
          }
        }
      },
      async changeLegendType(){
        const params = {
          type: 1,
          item: this.legendRadio,
          pid:  this.rowData.id
        };
        const data = await OutdoorTestApi.queryLegend(params);
        Object.assign(this.legendObj , data.data.object[0]);//保存图例相关信息
        if(data.data.object.length){
          this.legends[this.legendRadio].content = data.data.object[0].content;
          this.legends[this.legendRadio].index = data.data.object[0].content.length;
        }
      },

      //进度条
      dragBar(value){
        this.latlngIndex = ((this.latlngArray.length * value)/100).toFixed(0)!=0?((this.latlngArray.length * value)/100).toFixed(0)-1:0;
        this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.showHighLight(this.latlngArray[this.latlngIndex], this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
        this.showHighLight(this.latlngArray[this.latlngIndex], this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '1');
        }
        this.initLteInfo(this.latlngArray[this.latlngIndex].mid);
        this.percent = value;
        const lineIndex = this.lineMidList.indexOf(this.latlngArray[this.latlngIndex].mid);
        this.linePointClick(lineIndex,value);
        return false;
      },
      seekBar(value){
        this.latlngIndex = ((this.latlngArray.length * value)/100).toFixed(0)!=0?((this.latlngArray.length * value)/100).toFixed(0)-1:0;
        this.highLightPoint(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude);
        this.initLteInfo(this.latlngArray[this.latlngIndex].mid);
        this.showHighLight(this.latlngArray[this.latlngIndex], this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
        this.showHighLight(this.latlngArray[this.latlngIndex], this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(this.latlngArray[this.latlngIndex].latitude, this.latlngArray[this.latlngIndex].longitude,this.latlngArray[this.latlngIndex].mid, '1');
        }
        this.percent = value;
        const lineIndex = this.lineMidList.indexOf(this.latlngArray[this.latlngIndex].mid);
        this.linePointClick(lineIndex,value);
        return false;
      },

      async initLteInfo(id){
        const params = {
          id: id
        };
        const data = await OutdoorTestApi.querySixPane(params);
        if (data.data) {
          this.LteData = data.data;
        }
        if (this.httpCount == 1) {
          this.progressVal = 100;
          this.loading = false;

        } else {
          this.httpCount = 1;
        }
      },
      // 控制面板是否展示
      handleChange(value) {
        this.activePaneId = value;
        localStorage.setItem('paneId'+ this.rowData.id, JSON.stringify(value));
        if (this.initPaneList.indexOf(2) == -1 && value.indexOf(2) > -1) {
          this.initPaneList = this.activePaneId;
          this.queryLineInit();
        }
      },
      // 初始化获取面板设置
      getPaneId() {
        var showId = JSON.parse(localStorage.getItem('paneId'+ this.rowData.id));
        if(showId && showId.length > 0) {
          this.activePaneId = showId;
        } else {
          this.activePaneId = [1,2,3,4,5,6,7];
        }
        this.initPaneList = this.activePaneId;
        if (this.activePaneId.indexOf(2) > -1) {
          this.queryLineInit();
        }
      },
      // 信令做虚拟滚动
      hanldeScroll(e) {
        //   节流滚动计算
        if ( new Date().getTime()-this.lastTime > 20) {
          this.offSetY = e.target.scrollTop - (e.target.scrollTop % 30); //设置动态偏移量模拟滚动
          this.msgData = this.msgTableData.slice(
            Math.floor(e.target.scrollTop / 30),
            Math.floor(e.target.scrollTop / 30) + 20
          ); // 根据滚动条高度来截取需要展示的列表区间
          this.lastTime = new Date().getTime();
        }
      },
      // 事件做虚拟滚动
      hanldeScroll1(e) {
        //   节流滚动计算
        if ( new Date().getTime()-this.lastTime1 > 20) {
          this.offSetY1 = e.target.scrollTop - (e.target.scrollTop % 30); //设置动态偏移量模拟滚动
          this.eventData = this.eventTableData.slice(
            Math.floor(e.target.scrollTop / 30),
            Math.floor(e.target.scrollTop / 30) + 20
          ); // 根据滚动条高度来截取需要展示的列表区间
          this.lastTime1 = new Date().getTime();
        }
      },
      // 点击信令行事件
      clickMsg(record, divClick) {
        if (record.index === this.msgRecord.index) {
          return;
        }
        if (this.currentCircle.mid != record.mid) {
          this.currentCircle = {latitude: record.latitude, longitude: record.longitude, mid: record.mid};
        }
        if (divClick) {
          this.activeMsgVal = record.mMessageType;
        }
        this.msgRecord.active = false;
        this.activeMsgIndex = record.index;
        record.active = true;
        this.msgRecord = record;
        this.initLteInfo(record.mid);
        this.highLightPoint(record.latitude,record.longitude);
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(record.latitude,record.longitude,record.mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(record.latitude,record.longitude,record.mid, '1');
        }
        this.showHighLight(record, this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
        const lineIndex = this.lineMidList.indexOf(record.mid);
        const percent = ((lineIndex + 1)/this.lineMidList.length)* 100;
        this.linePointClick(lineIndex,percent);
      },
      // 点击事件行事件
      clickEvt(record, divClick) {
        if (record.index === this.eventRecord.index) {
          return;
        }
        if (this.currentCircle.mid != record.mid) {
          this.currentCircle = {latitude: record.latitude, longitude: record.longitude, mid: record.mid};
        }
        if (divClick) {
          this.activeEvtVal = record.mEventType;
        }
        this.eventRecord.active = false;
        this.activeEvtIndex = record.index;
        record.active = true;
        this.eventRecord = record;
        this.initLteInfo(record.mid);
        this.highLightPoint(record.latitude,record.longitude);
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(record.latitude,record.longitude,record.mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(record.latitude,record.longitude,record.mid, '1');
        }
        this.showHighLight(record, this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
        const lineIndex = this.lineMidList.indexOf(record.mid);
        const percent = ((lineIndex + 1)/this.lineMidList.length)* 100;
        this.linePointClick(lineIndex,percent);
      },
      // 重构展示高亮的方法
      showHighLight(record, allList, val, index, activeObj, time, y, list, divId) {
        var midIndex = allList.findIndex(v => v.mid == record.mid);
        if (midIndex == -1) {
          return;
        }
        if (this[activeObj] && this[activeObj].active) {
          this[activeObj].active = false;
          this[index] = -1;
        }
        if (record.mid == allList[midIndex].mid) {
          allList[midIndex].active = true;
          this[activeObj] = allList[midIndex];
          this[index] = allList[midIndex].index;
          if (divId == 'msgDiv') {
            this[val] = allList[midIndex].mMessageType;
          } else {
            this[val] = allList[midIndex].mEventType;
          }
          // 判断是否需要滚动滚动条
          const top = allList[midIndex].index*30;
          const height = document.getElementById(divId).offsetHeight + 30;
          const scrollTop = document.getElementById(divId).scrollTop;
          if (top < scrollTop ||  top > (scrollTop + height)) {
            this.getScroll(time, divId, midIndex, list, allList, y);
          }
        }
      },
      // 信令双击展示详情
      showDetail() {
        this.$refs.messageInfo.getMessageInfo();
      },
      // 信令向上查询匹配项
      searchMsgUp() {
        const list = this.msgTableData.slice(0,this.activeMsgIndex);
        const ifHave = list.some(v => v.mMessageType.indexOf(this.activeMsgVal) >  -1);
        if (ifHave) {
          for (var i = list.length - 1; i >= 0; i --) {
            if (list[i].mMessageType.indexOf(this.activeMsgVal) > -1) {
              this.activeMsgIndex = list[i].index;
              this.clickMsg(list[i],false);
              const top = list[i].index*30;
              const scrollTop = document.getElementById('msgDiv').scrollTop;
              if (top < scrollTop) {
                this.getScroll('lastTime', 'msgDiv', this.activeMsgIndex, 'msgData', this.msgTableData, 'offSetY');
              }
              break;
            }
          }
        } else {
          this.$warning({
            title: '提示',
            content: '向上没有更多匹配项了',
          });
        }
      },
      // 信令向下查询匹配项
      searchMsgDown() {
        const list = this.msgTableData.slice(this.activeMsgIndex + 1);
        const ifHave = list.some(v => v.mMessageType.indexOf(this.activeMsgVal) >  -1);
        if (ifHave) {
          for (var i = 0; i < list.length; i ++) {
            if (list[i].mMessageType.indexOf(this.activeMsgVal) > -1) {
              this.activeMsgIndex = list[i].index;
              this.clickMsg(list[i],false);
              const top = list[i].index*30;
              const divHeight = document.getElementById('msgDiv').offsetHeight + 30;
              const scrollTop = document.getElementById('msgDiv').scrollTop + divHeight;
              if (top > scrollTop) {
                this.getScroll('lastTime', 'msgDiv', this.activeMsgIndex, 'msgData', this.msgTableData, 'offSetY');
              }
              break;
            }
          }
        } else {
          this.$warning({
            title: '提示',
            content: '向下没有更多匹配项了',
          });
        }
      },
      // 事件向上查询匹配项
      searchEvtUp() {
        const list = this.eventTableData.slice(0,this.activeEvtIndex);
        const ifHave = list.some(v => v.mEventType.indexOf(this.activeEvtVal) >  -1);
        if (ifHave) {
          for (var i = list.length - 1; i >= 0; i --) {
            if (list[i].mEventType.indexOf(this.activeEvtVal) > -1) {
              this.activeEvtIndex = list[i].index;
              this.clickEvt(list[i],false);
              const top = list[i].index*30;
              const scrollTop = document.getElementById('evtDiv').scrollTop;
              if (top < scrollTop) {
                this.getScroll('lastTime1', 'evtDiv', this.activeEvtIndex, 'eventData', this.eventTableData, 'offSetY1');
              }
              break;
            }
          }
        } else {
          this.$warning({
            title: '提示',
            content: '向上没有更多匹配项了',
          });
        }
      },
      // 事件向下查询匹配项
      searchEvtDown() {
        const list = this.eventTableData.slice(this.activeEvtIndex + 1);
        const ifHave = list.some(v => v.mEventType.indexOf(this.activeEvtVal) >  -1);
        if (ifHave) {
          for (var i = 0; i < list.length; i ++) {
            if (list[i].mEventType.indexOf(this.activeEvtVal) > -1) {
              this.activeEvtIndex = list[i].index;
              this.clickEvt(list[i],false);
              const top = list[i].index*30;
              const divHeight = document.getElementById('evtDiv').offsetHeight + 30;
              const scrollTop = document.getElementById('evtDiv').scrollTop + divHeight;
              if (top > scrollTop) {
                this.getScroll('lastTime1', 'evtDiv', this.activeEvtIndex, 'eventData', this.eventTableData, 'offSetY1');
              }
              break;
            }
          }
        } else {
          this.$warning({
            title: '提示',
            content: '向下没有更多匹配项了',
          });
        }
      },

      // 设置滚动条滚动
      getScroll(time, div, index, data, list, y) {
        if ( new Date().getTime()-this[time] > 20) {
          document.getElementById(div).scrollTop = index*30 - 150;
          const top = document.getElementById(div).scrollTop;
          this[y] = top - (top % 30); //设置动态偏移量模拟滚动
          var start = index - 5;
          var end = index + 15;
          if (start <= 0) {
            this[data] = list.slice(0,20);
          } else if (end >= list.length) {
            this[data] = list.slice(start);
          } else {
            this[data] = list.slice(start,end);
          }
          this[time] = new Date().getTime();
        }
      },

      // 查询地图图层
      async queryMemory(){
        const data = await OutdoorTestApi.memoryConfigQuery({});
        const obj = data.data.object;
        this.layerData = {
          eventAbnormalFlag: obj.eventAbnormalFlag == '1'?true:false,
          eventNormalFlag: obj.eventNormalFlag == '1'?true:false,
          indoorFlag: obj.indoorFlag == '1'?true:false,
          neighborhoodFlag: obj.neighborhoodFlag == '1'?true:false,
          outdoorFlag: obj.outdoorFlag == '1'?true:false,
          serviceCellFlag: obj.serviceCellFlag == '1'?true:false,
          showPointValFlag: obj.showPointValFlag == '1'?true:false,
          stationFlag4g: obj.stationFlag4g == '1'?true:false,
          stationFlag5g: obj.stationFlag5g == '1'?true:false,
          id: obj.id,
          showPointSizeFlag: +obj.showPointSizeFlag
        };
        Object.assign(this.layerDataInit, this.layerData);
        if (obj.itemType) {
          this.legendRadio = obj.itemType;
        }
        this.pointSize = +obj.showPointSizeFlag;
        if (this.currentCircle.mid && this.layerData.stationFlag4g) {
          this.renderPolyLines(this.currentCircle.latitude,this.currentCircle.longitude, this.currentCircle.mid, '0');
        }
        if (this.currentCircle.mid && this.layerData.stationFlag5g) {
          this.renderPolyLines(this.currentCircle.latitude,this.currentCircle.longitude, this.currentCircle.mid, '1');
        }
        this.initLegend();
      },
      // 保存图层
      async saveLayer() {
        const params = {
          eventAbnormalFlag: this.layerData.eventAbnormalFlag?'1':'0',
          eventNormalFlag: this.layerData.eventNormalFlag?'1':'0',
          indoorFlag: this.layerData.indoorFlag?'1':'0',
          neighborhoodFlag: this.layerData.neighborhoodFlag?'1':'0',
          outdoorFlag: this.layerData.outdoorFlag?'1':'0',
          serviceCellFlag: this.layerData.serviceCellFlag?'1':'0',
          showPointValFlag: this.layerData.showPointValFlag?'1':'0',
          stationFlag4g: this.layerData.stationFlag4g?'1':'0',
          stationFlag5g: this.layerData.stationFlag5g?'1':'0',
          showPointSizeFlag: this.pointSize,
          id: this.layerData.id
        };
        const data = await OutdoorTestApi.memoryConfigEdit(params);
        if (data.data.success) {
          this.ifShowLayer = false;
          this.$message.success("图层修改成功！");
          this.queryMemory();
          this.ifShowCells();
          this.queryEventType();
        }
      },
      // 判断是否展示室内,室外点
      ifShowCells() {
        const zoom = map.getZoom();
        if (zoom > 14) {
          if(this.layerData.stationFlag4g) {
            if(this.layerData.indoorFlag && this.layerData.outdoorFlag) {
              this.renderCells('simgc', 'outdoor', 'Indoor');
            } else if (this.layerData.indoorFlag) {
              if (this.cellsOutdoor4g.length > 0) {
                for (let item of this.cellsOutdoor4g) {
                  mapUtils.removeLayer(item, map);
                }
              }
              this.renderCells('simgc', null, 'Indoor');
            } else if (this.layerData.outdoorFlag) {
              if (this.cellsIndoor4g.length > 0) {
                for (let item of this.cellsIndoor4g) {
                  mapUtils.removeLayer(item, map);
                }
              }
              this.renderCells('simgc', 'outdoor', null);
            }
          } else {

            if (this.cellsOutdoor4g.length > 0) {
              for (let item of this.cellsOutdoor4g) {
                mapUtils.removeLayer(item, map);
              }
            }

            if (this.cellsIndoor4g.length > 0) {
              for (let item of this.cellsIndoor4g) {
                mapUtils.removeLayer(item, map);
              }
            }

          }
          if(this.layerData.stationFlag5g) {
            if(this.layerData.indoorFlag && this.layerData.outdoorFlag) {
              this.renderCells('simgc5g', 'outdoor', 'Indoor');
            } else if (this.layerData.indoorFlag) {
              if (this.cellsOutdoor5g.length > 0) {
                for (let item of this.cellsOutdoor5g) {
                  mapUtils.removeLayer(item, map);
                }
              }
              this.renderCells('simgc5g', null, 'Indoor');
            } else if (this.layerData.outdoorFlag) {
              if (this.cellsIndoor5g.length > 0) {
                for (let item of this.cellsIndoor5g) {
                  mapUtils.removeLayer(item, map);
                }
              }
              this.renderCells('simgc5g', 'outdoor', null);
            }
          } else {
            if (this.cellsIndoor5g.length > 0) {
              for (let item of this.cellsIndoor5g) {
                mapUtils.removeLayer(item, map);
              }
            }
            if (this.cellsOutdoor5g.length > 0) {
              for (let item of this.cellsOutdoor5g) {
                mapUtils.removeLayer(item, map);
              }
            }
          }
        }
      },
      // 展示正常异常事件
      queryEventType(){
        // 移除正常事件覆盖物
        if (!this.layerData.eventNormalFlag && this.eventNormal.length > 0) {
          for (let item of this.eventNormal) {
            mapUtils.removeLayer(item,map);
          }
        }
        // 移除异常事件覆盖物
        if (!this.layerData.eventNormalFlag && this.eventAbnormal.length > 0) {
          for (let item of this.eventAbnormal) {
            mapUtils.removeLayer(item, map);
          }
        }
        const latlngs = [];
        this.mainLogData.map(x=>{
          if(x.normalEventType && this.layerData.eventNormalFlag){
            latlngs.push({
              latitude: x.latitude,
              longitude: x.longitude,
              event:x.normalEventType,
              type: 'normalEvent'
            })
          }
          if(x.abNormalEventType && this.layerData.eventAbnormalFlag){
            latlngs.push({
              latitude: x.latitude,
              longitude: x.longitude,
              event:x.abNormalEventType,
              type: 'abNormalEvent'
            })
          }
        });
        //去重经纬度
        const res =[];
        latlngs.map(i => {
          return Object.keys(i).map(k => i[k]).join('')
        }).filter((item, index, strarr) => {
          if (strarr.indexOf(item) === index) {
            res.push(latlngs[index]);
            this.highLightEventPoint(latlngs[index].latitude, latlngs[index].longitude,latlngs[index].event, latlngs[index].type);
          }
        });
      },
      // 点击小区连线
      aloneLine() {
        this.activeAlone = !this.activeAlone;
        if (!this.activeAlone && !this.activeAll) {
          this.lineCi = [];
          this.groupsList.forEach(v => v.group.clearLayers(map));
          this.groupsList = [];
        }
      },
      // 点击全网连线
      async allLine() {
        this.activeAll = !this.activeAll;
        this.lineCi = [];
        this.groupsList.forEach(v => v.group.clearLayers(map));
        this.groupsList = [];
        if (this.activeAll) {
          if(!this.layerData.stationFlag4g && !this.layerData.stationFlag5g) {
            return
          }
          var type = '0';
          if (this.layerData.stationFlag4g && this.layerData.stationFlag5g) {
            type = '';
          } else if (this.layerData.stationFlag5g) {
            type = '1';
          }
          const params = {
            gcType: type,
            pid: this.rowData.id
          };
          const data = await OutdoorTestApi.queryNetLine(params);
          if (data.data && data.data.length > 0) {
            for(let cell of data.data) {
              var line = {color: this.colorList[cell.pci % 3], weight: 1};
              let latlngs = [];
              var obj = cell;
              var points = cell.points;
              var polyLineGroup = new L.layerGroup();
              points.map(x => {
                latlngs = [[obj.lat, obj.lng], [x.lat, x.lng]];
                L.polyline(latlngs, line).addTo(polyLineGroup);
              });
              this.groupsList.push({ci: obj.ci, group: polyLineGroup});
              polyLineGroup.addTo(map);
              this.lineCi.push(obj.ci);
            }

          }
        }
      },
      // 点击圆点事件
      circleEvt(x,index,latlngs) {
        this.initLteInfo(x.mid);
        this.currentCircle = {latitude: x.latitude, longitude: x.longitude, mid: x.mid};
        this.showHighLight(this.currentCircle, this.eventTableData, 'activeEvtVal', 'activeEvtIndex', 'eventRecord', 'lastTime1', 'offSetY1', 'eventData', 'evtDiv');
        this.showHighLight(this.currentCircle, this.msgTableData, 'activeMsgVal', 'activeMsgIndex', 'msgRecord', 'lastTime', 'offSetY', 'msgData', 'msgDiv');
        if (this.layerData.stationFlag4g) {
          this.renderPolyLines(x.latitude,x.longitude,x.mid, '0');
        }
        if (this.layerData.stationFlag5g) {
          this.renderPolyLines(x.latitude,x.longitude,x.mid, '1');
        }
        this.percent = (index/latlngs.length).toFixed(2)*100;
        this.latlngIndex = index;
        this.highLightPoint(x.latitude, x.longitude);
        const lineIndex = this.lineMidList.indexOf(x.mid);
        const percent = ((lineIndex + 1)/this.lineMidList.length)* 100;
        this.linePointClick(lineIndex,percent);
      },
      // 图例取消重置
      cancelLegend() {
        this.ifShowLegend = false;
        this.legendRadio = this.legendData.item;
        this.showLegendParam = (this.legendObj.showFlag == '1')? true: false;
        this.showLegendDetailParam = (this.legendObj.pointDetail == '1')? true: false;
        this.legends[this.legendRadio].content = JSON.parse(JSON.stringify(this.legendData.content));
        this.legends[this.legendRadio].index = this.legendData.content.length;
      },
      // 点击图例
      showLegendPane() {
        this.ifShowLegend = !this.ifShowLegend;
        if (!this.ifShowLegend) {
          this.cancelLegend();
        }
      },
      // 图层取消
      cancelLayer() {
        this.ifShowLayer = false;
        Object.assign(this.layerData, this.layerDataInit);
        this.pointSize = this.layerDataInit.showPointSizeFlag;
      },
      // 点击图层
      showLayer() {
        this.ifShowLayer = !this.ifShowLayer;
        if (!this.ifShowLayer) {
          this.cancelLayer();
        }
      },
      // 关闭扇区详情
      closeDetail() {
        if (this.currentCell) {
          var oldImg = document.querySelector('img[alt="'+this.currentCell+'"]');
          if (this.currentCell.indexOf('5g') > -1) {
            oldImg.style.width = '31px';
            oldImg.style.height = '32px';
            oldImg.style.transformOrigin = '15px 32px';
            oldImg.style.marginLeft = '-15px';
            oldImg.style.marginTop = '-32px';
          } else {
            oldImg.style.width = '24px';
            oldImg.style.height = '25px';
            oldImg.style.transformOrigin = '12px 25px';
            oldImg.style.marginLeft = '-12px';
            oldImg.style.marginTop = '-25px';
          }
        }
        this.ifShowTable=false;
      },
      // 输入CI查询小区
      async queryByCi(value) {
        if (value) {
          const params = {
            ci: value
          };
          const data = await OutdoorTestApi.queryByCi(params);
          if(data.data && data.data.length > 0) {
            this.cellNameList = data.data.map(x => {
              return {
                siteName:x.lte_ci,
                value:x.lte_net+x.pk
              }
            });
            this.cellName = this.cellNameList[0].value;
            this.siteInfoData = data.data[0];
            map.setView([data.data[0].lte_latitude2, data.data[0].lte_longitude2]);
            var flag = (data.data.lte_net == 'LTE');
            var img;
            var ciVal;
            if (flag) {
              img = document.querySelector('img[alt="4g'+data.data[0].lte_ci+'"]');
              ciVal = '4g' + data.data[0].lte_ci;
            } else {
              img = document.querySelector('img[alt="5g'+data.data[0].lte_ci+'"]');
              ciVal = '5g' + data.data[0].lte_ci;
            }
            this.activeCell(flag, ciVal,img);
          } else {
            this.cellNameList = [];
          }
          this.ifShowTable = true;
        }
      },
      // 扇区选中效果
      activeCell(if4g,ci,img) {
        if (this.currentCell) {
          var oldImg = document.querySelector('img[alt="'+this.currentCell+'"]');
          if (this.currentCell.indexOf('5g') > -1) {
            oldImg.style.width = '31px';
            oldImg.style.height = '32px';
            oldImg.style.transformOrigin = '15px 32px';
            oldImg.style.marginLeft = '-15px';
            oldImg.style.marginTop = '-32px';
          } else {
            oldImg.style.width = '24px';
            oldImg.style.height = '25px';
            oldImg.style.transformOrigin = '12px 25px';
            oldImg.style.marginLeft = '-12px';
            oldImg.style.marginTop = '-25px';
          }
        }
        if (if4g) {
          img.style.width = '34px';
          img.style.height = '35px';
          img.style.transformOrigin = '17px 35px';
          img.style.marginLeft = '-17px';
          img.style.marginTop = '-35px';
        } else {
          img.style.width = '41px';
          img.style.height = '42px';
          img.style.transformOrigin = '20px 42px';
          img.style.marginLeft = '-20px';
          img.style.marginTop = '-42px';
        }
        this.currentCell = ci;
      },
      // 双击小区事件
      async dblclickCell(e, type, colorVal) {
        clearTimeout(this.timeClick);
        if(this.activeAlone) {
          const params = {
            longitude: e.target.options.lng,
            latitude: e.target.options.lat,
            azimuth: e.target.options.rotationAngle,
            gcType: type,
            pid: this.rowData.id
          };
          const data = await OutdoorTestApi.queryNetLine(params);
          if (data.data && data.data.length > 0) {
            var line = {color: colorVal, weight: 1};
            let latlngs = [];
            var obj = data.data[0];
            var points = data.data[0].points;
            var polyLineGroup = new L.layerGroup();
            if(this.lineCi.indexOf(obj.ci) == -1){
              points.map(x => {
                latlngs = [[obj.lat, obj.lng], [x.lat, x.lng]];
                L.polyline(latlngs, line).addTo(polyLineGroup);
              });
              this.groupsList.push({ci: obj.ci, group: polyLineGroup});
              polyLineGroup.addTo(map);
              this.lineCi.push(obj.ci);
            } else {
              var currentObj = this.groupsList.filter(v => v.ci== obj.ci);
              currentObj[0].group.clearLayers(map);
              this.groupsList = this.groupsList.filter(v => v.ci!= obj.ci);
              this.lineCi = this.lineCi.filter(v => v!= obj.ci);
            }
          }
        }
      },
      // 折线指标查询
      async queryLineInit() {
          const data = await OutdoorTestApi.queryLine({});
          if (data.data && data.data.object) {
            var datas = data.data.object;
            this.lineDataInit = {
              type1: datas.itemTypeOne,
              type2: datas.itemTypeTwo,
              content: [
                {name: 'RSRP', min: datas.rsrpMin, max: datas.rsrpMax},
                {name: 'SINR', min: datas.sinrMin, max: datas.sinrMax},
                {name: 'SSRSRP', min: datas.ssRsrpMin, max: datas.ssRsrpMax},
                {name: 'SSSINR', min: datas.ssSinrMin, max: datas.ssSinrMax},
                {name: 'UL', min: datas.ssUlMin, max: datas.ssUlMax},
                {name: 'DL', min: datas.ssDlMin, max: datas.ssDlMax}
              ]
            };
            this.lineData = JSON.parse(JSON.stringify(this.lineDataInit));
          }
          this.queryLineData();
      },
      // 折线指标保存
      async saveLine() {
        const params = {
          itemTypeOne: this.lineData.type1,
          itemTypeTwo: this.lineData.type2?this.lineData.type2:'',
          rsrpMin: this.lineData.content[0].min,
          rsrpMax: this.lineData.content[0].max,
          sinrMin: this.lineData.content[1].min,
          sinrMax: this.lineData.content[1].max,
          ssRsrpMin: this.lineData.content[2].min,
          ssRsrpMax: this.lineData.content[2].max,
          ssSinrMin: this.lineData.content[3].min,
          ssSinrMax: this.lineData.content[3].max,
          ssUlMin: this.lineData.content[4].min,
          ssUlMax: this.lineData.content[4].max,
          ssDlMin: this.lineData.content[5].min,
          ssDlMax: this.lineData.content[5].max
        };
        const data = await OutdoorTestApi.editLine(params);
        if (data.data.success) {
          this.$message.success("指标修改成功！");
          this.showLinePane = false;
          this.queryLineInit();
        }
      },
      // 取消指标
      cancelLine() {
        this.showLinePane = !this.showLinePane;
        this.lineData = JSON.parse(JSON.stringify(this.lineDataInit));
      },
      // 折线统计数据查询
      async queryLineData() {
        const params = {
          pid: this.rowData.id,
          itemTypeOne: this.lineData.type1,
          itemTypeTwo: this.lineData.type2
        };
        const data = await OutdoorTestApi.lineData(params);
        const datas = data.data;
        var timeData = [];
        this.lineMidList = data.data.mid || [];
        for (let item of datas.time) {
          timeData.push(item.split(' ')[1]);
        }
        var data1 = datas.itemOneData.data;
        var data2 = null;
        var name1 = datas.itemOneData.name;
        var name2 = '';
        if (this.lineData.type2) {
          data2 = datas.itemTwoData.data;
          name2 = datas.itemTwoData.name;
        }
        var oneMax;
        var oneMin;
        var twoMax;
        var twoMix;
        for(let item of this.lineDataInit.content) {
          if (item.name === datas.itemOneData.name) {
            oneMax = item.max;
            oneMin = item.min;
          }
          if (name2 && item.name === name2) {
            twoMax = item.max;
            twoMix = item.min;
          }
        }
        const val = datas.time.length/180;
        if (val < 1) {
          this.lineVal = 100;
        } else {
          this.lineVal = (100 / val);
        }
        chartUtils.initLine("lineChartContain", timeData, data1, data2,name1,name2,oneMax,oneMin,twoMax,twoMix,this.lineVal);
        if (this.httpCount == 1) {
          this.progressVal = 100;
          this.loading = false;
        } else {
          this.httpCount = 1;
        }
      },
      // 点击折线图
      linePointClick(index,percent) {
        lineChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: index
        });
        var startIndex = 0;
        var endIndex = this.lineVal;
        if (percent > this.lineVal && percent < (100 - this.lineVal)) {
          startIndex = percent;
          endIndex = percent + this.lineVal;
        }
        if ( percent > (100 - this.lineVal)) {
          startIndex = 100 - this.lineVal;
          endIndex = 100;
        }
        lineChart.dispatchAction({
          type: 'dataZoom',
          start: startIndex,
          end: endIndex
        });
      },
      resizedEventMap() {
        map.invalidateSize(true);
      },
      resizedEventLine() {
        lineChart.resize();
      },
      onResizeStop: function(x, y, width, height) {
        this.layout[this.activePaneIndex].w = width;
        this.layout[this.activePaneIndex].h = height;
      },
      onDragStop: function(x, y, width, height) {
        this.layout[this.activePaneIndex].x = x;
        this.layout[this.activePaneIndex].y = y;
      }

    }
  }
</script>

<style>
  @import "../../../../static/css/style.css";
  .my-div-icon{
    font-size:12px;
    /*background:red;*/
    /*width:5px;*/
    color:white;
  }

  #outdoorTest .ant-btn-default {
    background: rgb(105, 116, 124);
    color: white;
  }
  #outdoorTest .ant-btn-default:hover {
    background: #586268;
    color: white;
  }
  .spin-contain{
    position: absolute;
    top: 40%;
    z-index:9999;
    left:50%;
    border: 1px solid #91d5ff;
    background-color: #e6f7ff;
    padding: 10px 30px;
  }
  .shallow{
    background:#e6f7ff;
    font-weight: bold;
  }
  .ant-card-extra{
    width: 70%;
    margin-right: 20px;
  }
  .leaflet-marker{
    top: 160px;
    left: 10px;
    background: white;
  }
  .leaflet-marker:hover{
    background:  rgba(150,150,150,0.1);
  }


  .ivu-select-default.ivu-select-multiple .ivu-select-selection{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .ivu-select-group-title{
    text-align: left;
    font-weight: bold;
  }
  .leaflet-legend{
    position: absolute;
    right: 0.5%;
    bottom: 4%;
    z-index: 999;
    cursor: pointer;
  }
  .procress-bar{
    display: inline-block;
    vertical-align: middle
  }
  table {
    border-collapse: collapse;
    width:100%;
  }
  table td,table th {
    border:1px solid #dddddd;
    padding:5px 10px;
    /*width:auto;*/
    text-align: left;
  }
  .leaflet-table{
    background: white;
    width:340px;
    height:310px;
    padding:5px;
    position: absolute !important;
    z-index: 999 !important;
    right: 2% !important;
    top: 2% !important;
    text-align: left;
    overflow: auto;
  }
  .leaflet-table p{
    margin: 5px;
    font-size: 12px;
    font-weight: bold;
  }
  .block1 {
    width: 32%;
    position: relative;
    height: 450px;
  }
  .selectPane {
   text-align: right;
    position: absolute;
    top: 3px;
    right: 50px;
  }
  .showPanes {
    position: absolute;
    top: 2px;
    right: 100px;
    width: 200px;
    height: 27px;
    cursor: pointer;
    z-index: 2;
    line-height: 27px;
    background: blue;
    border-radius: 4px;
    color: #fff;
  }
  .mapDefaultWidth {
    width: 40%;
  }
  .chartDefaultWidth {
    width: 27%;
  }
  .xlDefaultWidth {
    width: 30%;
  }
  .block1 .ant-card-body {
    height: calc(100% - 60px);
  }
  .xngd,
  .sjgd{
    display: flex;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
  }
  .xngd>div,
  .sjgd>div {
    padding: 6px;
    border-right: 1px solid #e8e8e8;
    line-height: 19px;
    width: 120px;
  }
  .xngd>div:nth-child(1) {
    border-left: 1px solid #e8e8e8;
    width: 80px;
  }
  .sjgd>div:nth-child(2) {
    width: 180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .xngd>div:nth-child(2),
  .sjgd>div:nth-child(1){
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .loading-contain {
    position: absolute;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,0.3);
    z-index: 9999;
  }
  .ant-progress-text {
    color: #fff;
  }
  .leaflet-control-attribution.leaflet-control,
  .leaflet-control-scale-line{
    display: none;
  }
  .legendSetting {
    z-index: 999;
    position: absolute;
    background: #fff;
    right: 20px;
    margin-left: -125px;
    top: 10px;
    padding: 10px;
    bottom: 10px;
    overflow: auto;
  }
  .addLegend {
    cursor: pointer;
    color: #1890ff;
    margin-right: 15px;
  }
  .deleteLegend {
    cursor: pointer;
    color: red;
  }
  .notDelete {
    cursor: not-allowed;
    color: #999;
  }
  .cancelLegend {
    cursor: pointer;
    color: gray;
  }
  .colorContain>td {
    padding: 0;
  }
  .showColor {
    padding: 0;
    border: 0;
  }
  .colorContain>td:nth-child(2)>.showColor,
  .colorContain>td:nth-child(3)>.showColor{
    width: 70px;
    padding: 0 10px;
  }
  .ant-select-dropdown {
    z-index: 10000;
  }
  .legendModule {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 999;
    background: rgba(255,255,255,0.7);
    text-align: right;
  }
  .showColorBlock {
    width: 60px;
    height: 19px;
  }
  .leaflet-control-zoom.leaflet-bar.leaflet-control {
    width: 0;
    height: 0;
    overflow: hidden;
    z-index: 0;
    display: none;
  }
  .mapLeftTools {
    position: absolute;
    left: 10px;
    z-index: 999;
    background: #fff;
    border: 1px solid #1890ff;
    border-radius: 4px;
    line-height: 29px;
    width: 51px;
    text-align: center;
    color: #1890ff;
    cursor: pointer;
  }
  .mapLeftToolsActive {
    position: absolute;
    left: 10px;
    z-index: 999;
    background: #1890ff;
    border: 1px solid #1890ff;
    border-radius: 4px;
    line-height: 29px;
    width: 51px;
    text-align: center;
    color: #fff;
    cursor: pointer;
  }
  .polyline-measure-unicode-icon {
    font-size: 12px;
    color: #1890ff !important;
    background: #fff;
    width: 100% !important;
    line-height: 26px;
    border-radius: 4px;
    font-weight: normal !important;
    background-position-x: 0;
    background-position-y: 0;
  }
  .leaflet-touch .leaflet-control-layers, .leaflet-touch .leaflet-bar {
    border: 1px solid #1890ff;
    border-radius: 4px;
  }
  a.polyline-measure-controlOnBgColor, a.polyline-measure-controlOnBgColor:hover {
    background: #1890ff;
    color: #fff !important;
  }
  .leaflet-bar.leaflet-control {
    width: 52px !important;
    top: 40px;
  }
  .showPoint {
    text-align: center;
    height: 27px;
  }
  .ant-select-selection-selected-value>.showPoint {
    width: 50px;
  }
  .point0 {
    background: url("../../../../static/img/point1.png") center no-repeat;
  }
  .point1 {
    background: url("../../../../static/img/point2.png") center no-repeat;
  }
  .point2 {
    background: url("../../../../static/img/point3.png") center no-repeat;
  }
  .point3 {
    background: url("../../../../static/img/point4.png") center no-repeat;
  }
  .point4 {
    background: url("../../../../static/img/point5.png") center no-repeat;
  }
  .ciSearch {
    position: absolute;
    left: 10px;
    z-index: 999;
    top: 10px;
  }
  .lineActive {
    background: #e6f7ff;
  }
  .my-div-icon {
    -webkit-transform: scale(0.8);
  }
  .showDetail>div>p {
    display: inline-block;
    width: 45%;
  }
  .my-div-icon>span {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    display: block;
  }
  .lineSetModal {
    position: absolute;
    top: 20px;
    bottom: 20px;
    right: 20px;
    overflow: auto;
    background: #fff;
    z-index: 999;
    width: 300px;
    box-shadow: 0 0 10px 5px rgba(233, 230, 230, 0.9);
  }
  .showBoth {
    display: flex;
    justify-content: space-between;
  }
  .lineSetModal td {
    white-space: nowrap;
  }
  .ant-card-head {
    padding: 0;
  }
  .ant-card-head-title {
    cursor: move;
    padding-left: 20px;
  }
  .header-btn {
    position: absolute;
    top: 8px;
    right: 20px;
  }
  .ant-card-body {
    height: calc(100% - 50px);
    padding: 0 !important;
  }
  .vdr {
    border: 0 !important;
  }
  .active.vdr {
    z-index: 1002 !important;
  }
  .sameTable {
    height: 100%;
    overflow: auto;
  }
  .sameTr {
    display: flex;
  }
  .sameTr>div {
    width: 50%;
    border-top: 1px solid #dddddd;
    border-left: 1px solid #dddddd;
    line-height: 18px;
    height: 30px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding: 6px;
    box-sizing: border-box;
  }
  .sameTable .ant-col-12:nth-last-child(2),
  .sameTable .ant-col-12:nth-last-child(1){
    border-bottom: 1px solid #dddddd;
  }
  .sameTable .ant-col-12:nth-child(2n)>div:last-child,
  .sameTable .ant-col-12:last-child>div:last-child{
    border-right: 1px solid #dddddd;
  }
  .textOverflow {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  @media screen and (max-width: 1920px) {
    .top1Jj {
      top: 80px;
    }
    .top2Jj {
      top: 115px;
    }
  }
  @media screen and (max-width: 1536px) {
    .top1Jj {
      top: 71px;
    }
    .top2Jj {
      top: 100px;
    }
  }

</style>



